.rc-md-navigation {
  min-height: 38px;
  padding: 0 8px;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  border-bottom: 1px solid #e0e0e0;
  font-size: 16px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  -webkit-flex-direction: row;
  flex-direction: row;
  -webkit-box-pack: justify;
  -webkit-justify-content: space-between;
  justify-content: space-between;
}

.rc-md-navigation.in-visible {
  display: none;
}

.rc-md-navigation .navigation-nav {
  -webkit-box-align: center;
  -webkit-align-items: center;
  align-items: center;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  font-size: 14px;
  color: #757575;
}

.rc-md-navigation .button-wrap,
.rc-md-navigation .navigation-nav {
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  -webkit-flex-direction: row;
  flex-direction: row;
}

.rc-md-navigation .button-wrap {
  -webkit-flex-wrap: wrap;
  flex-wrap: wrap;
}

.rc-md-navigation .button-wrap .button {
  position: relative;
  min-width: 24px;
  height: 28px;
  margin-right: 5px;
  display: inline-block;
  cursor: pointer;
  line-height: 28px;
  text-align: center;
  color: #757575;
}

.rc-md-navigation .button-wrap .button:hover {
  color: #212121;
}

.rc-md-navigation .button-wrap .button.disabled {
  color: #bdbdbd;
  cursor: not-allowed;
}

.rc-md-navigation .button-wrap .rmel-iconfont {
  font-size: 18px;
}

.rc-md-navigation li,
.rc-md-navigation ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.rc-md-navigation .h1,
.rc-md-navigation .h2,
.rc-md-navigation .h3,
.rc-md-navigation .h4,
.rc-md-navigation .h5,
.rc-md-navigation .h6,
.rc-md-navigation h1,
.rc-md-navigation h2,
.rc-md-navigation h3,
.rc-md-navigation h4,
.rc-md-navigation h5,
.rc-md-navigation h6 {
  font-family: inherit;
  font-weight: 500;
  color: inherit;
  padding: 0;
  margin: 0;
  line-height: 1.1;
}

.rc-md-navigation h1 {
  font-size: 34px;
}

.rc-md-navigation h2 {
  font-size: 30px;
}

.rc-md-navigation h3 {
  font-size: 24px;
}

.rc-md-navigation h4 {
  font-size: 18px;
}

.rc-md-navigation h5 {
  font-size: 14px;
}

.rc-md-navigation h6 {
  font-size: 12px;
}

.rc-md-editor .tool-bar {
  position: absolute;
  z-index: 1;
  right: 8px;
  top: 8px;
}

.rc-md-editor .tool-bar .button {
  min-width: 24px;
  height: 28px;
  margin-right: 5px;
  display: inline-block;
  cursor: pointer;
  font-size: 14px;
  line-height: 28px;
  text-align: center;
  color: #999;
}

.rc-md-editor .tool-bar .button:hover {
  color: #333;
}

.rc-md-editor {
  padding-bottom: 1px;
  position: relative;
  border: 1px solid #e0e0e0;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-direction: column;
  flex-direction: column;
}

.rc-md-editor.full {
  width: 100%;
  height: 100% !important;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 1000;
}

.rc-md-editor .editor-container {
  -webkit-box-flex: 1;
  -webkit-flex: 1;
  flex: 1;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  width: 100%;
  min-height: 0;
  position: relative;
}

.rc-md-editor .editor-container > .section.in-visible {
  display: none;
}

.rc-md-editor .editor-container > .section > .section-container {
  padding: 10px 15px 15px;
}

.rc-md-editor .editor-container .sec-md {
  -webkit-box-flex: 1;
  -webkit-flex: 1;
  flex: 1;
  min-height: 0;
  min-width: 0;
}

.rc-md-editor .editor-container .sec-md .input {
  display: block;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  overflow-y: scroll;
  border: none;
  resize: none;
  outline: none;
  min-height: 0;
  color: #333;
  font-size: 14px;
  line-height: 1.7;
}

.rc-md-editor .editor-container .sec-html {
  -webkit-box-flex: 1;
  -webkit-flex: 1;
  flex: 1;
  min-height: 0;
  min-width: 0;
}

.rc-md-editor .editor-container .sec-html .html-wrap {
  height: 100%;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  overflow: auto;
}

.custom-html-style {
  color: #333;
}

.custom-html-style h1 {
  font-size: 32px;
  padding: 0;
  border: none;
  font-weight: 700;
  margin: 32px 0;
  line-height: 1.2;
}

.custom-html-style h2 {
  font-size: 24px;
  padding: 0 0;
  border: none;
  font-weight: 700;
  margin: 24px 0;
  line-height: 1.7;
}

.custom-html-style h3 {
  font-size: 18px;
  margin: 18px 0;
  padding: 0 0;
  line-height: 1.7;
  border: none;
}

.custom-html-style p {
  font-size: 14px;
  line-height: 1.7;
  margin: 8px 0;
}

.custom-html-style a {
  color: #0052d9;
}

.custom-html-style a:hover {
  text-decoration: none;
}

.custom-html-style strong {
  font-weight: 700;
}

.custom-html-style ol,
.custom-html-style ul {
  font-size: 14px;
  line-height: 28px;
  padding-left: 36px;
}

.custom-html-style li {
  margin-bottom: 8px;
  line-height: 1.7;
}

.custom-html-style hr {
  margin-top: 20px;
  margin-bottom: 20px;
  border: 0;
  border-top: 1px solid #eee;
}

.custom-html-style pre {
  display: block;
  padding: 20px;
  line-height: 28px;
  word-break: break-word;
}

.custom-html-style code,
.custom-html-style pre {
  font-size: 14px;
  border-radius: 0;
  overflow-x: auto;
}

.custom-html-style code {
  padding: 3px 0;
  margin: 0;
  word-break: normal;
}

.custom-html-style code:after,
.custom-html-style code:before {
  letter-spacing: 0;
}

.custom-html-style blockquote {
  position: relative;
  margin: 16px 0;
  padding: 5px 8px 5px 30px;
  border: none;
  color: #333;
  border-left: 10px solid #d6dbdf;
}

.custom-html-style img,
.custom-html-style video {
  max-width: 100%;
}

.custom-html-style table {
  font-size: 14px;
  line-height: 1.7;
  max-width: 100%;
  overflow: auto;
  border: 1px solid #f6f6f6;
  border-collapse: collapse;
  border-spacing: 0;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

.custom-html-style table td,
.custom-html-style table th {
  word-break: break-all;
  word-wrap: break-word;
  white-space: normal;
}

.custom-html-style table tr {
  border: 1px solid #efefef;
}

.custom-html-style table tr:nth-child(2n) {
}

.custom-html-style table th {
  text-align: center;
  font-weight: 700;
  border: 1px solid #efefef;
  padding: 10px 6px;
  word-break: break-word;
}

.custom-html-style table td {
  border: 1px solid #efefef;
  text-align: left;
  padding: 10px 15px;
  word-break: break-word;
  min-width: 60px;
}

.rc-md-editor .drop-wrap {
  display: block;
  position: absolute;
  left: 0;
  top: 28px;
  z-index: 2;
  min-width: 20px;
  padding: 10px 0;
  text-align: center;
  border: 1px solid #f1f1f1;
  border-right-color: #ddd;
  border-bottom-color: #ddd;
}

.rc-md-editor .drop-wrap.hidden {
  display: none !important;
}

.rc-md-editor .header-list .list-item {
  width: 100px;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  padding: 8px 0;
}

.rc-md-editor .table-list.wrap {
  position: relative;
  margin: 0 10px;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

.rc-md-editor .table-list.wrap .list-item {
  position: absolute;
  top: 0;
  left: 0;
  display: inline-block;
  width: 20px;
  height: 20px;
  border-radius: 3px;
}

.mde-preview li,
.custom-html-style li {
  list-style-type: inherit;
}

.react-mde .mde-header .mde-tabs button {
  margin-top: 12px;
  padding: 0 15px;
  &:focus{
    outline: none;
    border: 1px solid #000000;
    border-bottom: none;
  }
}

.react-mde  .mde-header .mde-tabs button.selected {
  background: white;
  margin-bottom: -2px;
  border: 1px solid #c8ccd0;
  border-bottom: none;
}